<template>
    <div id="r-info-kp">
        <div class="columns">
            <div class="column is-one-third" :style="`height: ${maxHeight}px; overflow: auto`">
                <div class="r-white-box">
                   <p class="r-info-title">Bacaan Utama</p>
                   <div class="r-info-button" :class="activeTab == 'main' ? 'r-isactive-green' : ''" @click.prevent="setPdf(infoData.link_policy,'main')">{{infoData.title}}</div>
                   <p class="r-info-title">Gambar</p>
                   <div class="r-info-button" :class="activeTab == `img-${pic.id}` ? 'r-isactive-green' : ''" @click.prevent="setPicture(pic.link_doc,pic.id)" v-for="pic in detailData.image" :key="`img-${pic.id}`">{{pic.name}}</div>
                   <p class="r-info-title">Lampiran</p>
                   <div class="r-info-button" :class="activeTab == `doc-${doc.id}` ? 'r-isactive-green' : ''" @click.prevent="setPdf(doc.link_doc,`doc-${doc.id}`)" v-for="doc in detailData.doc" :key="`doc-${doc.id}`">{{doc.name}}</div>
                </div>
            </div>
            <div class="column is-two-thirds" v-if="show == 'pdf'"  :style="`height: ${maxHeight}px; padding-left: 20px !important;`">
                <pdf :path="pdfpath"></pdf>
            </div>
            <div class="column is-two-thirds r-custom-scroll" v-if="show == 'picture'"  :style="`height: ${maxHeight}px; padding-left: 20px !important;`">
              <img :src="picture" alt="">
            </div>
        </div>
    </div>
</template>
<script>
function loadItem  (view) {
  return () => import(/* webpackChunkName: "view-[request]" */ `./${view}.vue`)
}
export default {
   props:{
    data: {
      type: Object,
      default: () => ({
        name: null
      })
    },
    info: {
      type: Object,
      default: () => ({
        title: null
      })
    }
  },
  components: {
    pdf: loadItem('pdf')
  },
  mounted() {
    this.setPdf(this.infoData.link_policy,'main')
  },
  data: () => ({
    show: 'pdf',
    picturepath: null,
    updatePath: null,
    activeTab: 'main'
  }),
  watch: {
    info(){
      this.setPdf(this.infoData.link_policy,'main')
    }
  },
  computed: {
    maxHeight(){
      return window.innerHeight - 280;
    },
    picture(){
      return process.env.VUE_APP_Path+this.picturepath
    },
    detailData(){
      return this.data
    },
    infoData(){
      return this.info
    },
    pdfpath(){
      return this.updatePath
    }
  },
  methods: {
    setPdf(path,type){
      this.show = null
      this.updatePath = path
      this.activeTab = type
      this.$nextTick(() => {
        this.show = 'pdf'
      })
    },
    setPicture(path,id){
      this.show = 'picture'
      this.picturepath = path
      this.activeTab = `img-${id}`
    }
  },
}
</script>
<style lang="scss">
.r-white-box{
    .r-info-title{
      font-family: Roboto;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      color: #000000;
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .r-info-button{
      background: #F4F4F4;
      color: black;
      padding: 5px;
      margin-top: 10px;
      box-sizing: border-box;
      border: 1px solid #F4F4F4;
      cursor: pointer;
      &:hover{
        background: #A3CEC7;
        border: 1px solid #00573D;
      }
    }
}
</style>
